.image-toolbar {
  position: absolute;
  margin: 5px;
  -webkit-transition: opacity 400ms;
  -o-transition: opacity 400ms;
  transition: opacity 400ms; }
  .image-toolbar.docked {
    z-index: 4; }
  .image-toolbar .full-zoom-button {
    padding-left: 0;
    padding-right: 0;
    font-weight: bold;
    font-size: smaller; }
    .image-toolbar .full-zoom-button span {
      color: #5c7080; }
  .image-toolbar .link-button {
    width: 30px; }
    .image-toolbar .link-button .bp3-button-text {
      position: absolute;
      top: 0;
      left: 16px;
      font-size: smaller;
      color: #5c7080; }
    .image-toolbar .link-button .bp3-icon {
      -webkit-transform: scale(-1, 1);
          -ms-transform: scale(-1, 1);
              transform: scale(-1, 1); }
  .image-toolbar.bp3-dark .full-zoom-button .bp3-button-text {
    color: #bfccd6; }
  .image-toolbar.bp3-dark .link-button .bp3-button-text {
    color: #bfccd6; }



