.catalog-plot {
  width: 100%;
  height: 100%; }
  .catalog-plot .catalog-plot-option {
    width: 100%;
    height: 40px;
    display: -ms-inline-flexbox;
    display: inline-flex;
    padding-left: 5px;
    padding-top: 5px; }
    .catalog-plot .catalog-plot-option .bp3-form-content {
      margin-right: 5px; }
    .catalog-plot .catalog-plot-option .catalog-bins .bp3-form-content .bp3-control-group .bp3-input-group {
      width: 100px; }
  .catalog-plot .non-ideal-state {
    height: calc(100% - 40px); }
  .catalog-plot .catalog-plotly {
    width: 100%;
    height: calc(100% - 90px);
    position: relative; }
    .catalog-plot .catalog-plotly .js-plotly-plot .plotly .hoverlayer .spikeline {
      stroke: #738694 !important;
      stroke-width: 1px; }
  .catalog-plot .catalog-plotly-dark {
    width: calc(100% - 10px);
    height: calc(100% - 90px);
    position: relative; }
    .catalog-plot .catalog-plotly-dark .js-plotly-plot .plotly .hoverlayer .spikeline {
      stroke: #a7b6c2 !important;
      stroke-width: 1px; }
  .catalog-plot .catalog-plot-footer {
    width: 100%;
    height: 50px; }
    .catalog-plot .catalog-plot-footer .scatter-info {
      height: 15px;
      width: 100%;
      position: relative; }
    .catalog-plot .catalog-plot-footer .actions {
      display: -ms-inline-flexbox;
      display: inline-flex;
      float: right;
      padding-right: 5px;
      height: 35px; }



