.draggable-dialog {
  top: 0;
  left: 0;
  position: fixed;
  z-index: 100;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .draggable-dialog .bp3-overlay.bp3-overlay-scroll-container.bp3-overlay-inline {
    position: static;
    height: 100%;
    width: 100%; }
  .draggable-dialog .bp3-overlay-inline .bp3-overlay-content, .draggable-dialog .bp3-overlay-scroll-container .bp3-overlay-content {
    position: static;
    width: 100%;
    height: 100%; }
  .draggable-dialog .bp3-dialog-container .bp3-dialog {
    position: static;
    width: 100%;
    height: 100%;
    margin: 0 !important; }
  .draggable-dialog .bp3-dialog-header {
    cursor: move; }
  .draggable-dialog .bp3-dialog-body {
    height: 100%;
    width: auto;
    overflow: auto; }
  .draggable-dialog .bp3-overlay-backdrop {
    display: none; }



